<template>
  <div id="ParseURL">
    <div class="inputBox">
      <div class="input">
        <input type="text" v-model="value" @keyup.enter="getQuery(value)">
      </div>
      <div class="inputBtn">
        <button @mouseup="getQuery(value)">
          <h3>go</h3>
        </button>
      </div>
    </div>
    <div class="viewBox">
      <span></span>
      <div class="view">
        <textarea readonly :value="query.id + ' , ' + query.wfr + ' , ' + query.for"></textarea>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name:'ParseURL',
  components: {

  },
  props:{
    before:{
      type:Boolean,
      default:true
    }
  },
  data() {
    return {
      value:"x.com?id=1731009253527855883&wfr=spider&for=pc",
      query:{
        id:"",
        wfr:"",
        for:""
      }
    }
  },
  mounted(){

  },
  methods:{
    getQuery(url){
      if(this.before == false){
        const withoutDomain = url.split('?')[1];
        let searchParams = new URLSearchParams(withoutDomain);
        let id = searchParams.get('id')
        let wfr = searchParams.get('wfr')
        let For = searchParams.get('for')
        let querys = {
          id: "id : " + id ,
          wfr: "wfr : " + wfr ,
          for: "for : " + For ,
        }
        this.query = querys
      }
    },
  },
}
</script>

<style scoped>
#ParseURL{
  width: 100%;
}
.inputBox{
  width: 50%;
  display: flex;
}
.input{
  width: calc(100% - 45px);
  height: 30px;
}
.inputBtn{
  width: 45px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.inputBtn button{
  width: 30px;
  height: 30px;
  cursor: pointer;
  border: 2px solid white;
  border-radius: 5px;
  background: none;
  color: white;
}
.input input{
  width: calc(100% - 30px);
  border: none;
  padding: 0 15px;
  height: 100%;
  border-radius: 5px;
  background: rgb(41, 44, 57);
  color: white;
  border: 1px solid rgb(72, 72, 72);
}
.viewBox{
  width: 50%;
  background: rgb(41, 44, 57);
  border-radius: 5px;
  margin-top: 10px;
  border: 1px solid rgb(72, 72, 72);
}
.viewBox span{
  width: 100%;
}
.view{
  width: 100%;
  height: 170px;
}
.view textarea{
  width: calc(100% - 30px);
  height: 100%;
  resize:none;
  padding: 0 15px;
  background: rgb(41, 44, 57);
  border: none;
  color: white;
}
</style>