<template>
  <div id="Promise">
    <h4>30AB</h4>
    <h4>30AB</h4>
    <div class="inputBox">
      <div class="input">
        <input type="text" v-model="value" @keyup.enter="Cfun(value)">
      </div>
      <div class="inputBtn">
        <button @mouseup="Cfun(value)">
          <h3>go</h3>
        </button>
      </div>
    </div> 
    
  </div>
</template>

<script>

export default {
  name:'Promise',
  components: {

  },
  props:{
    before:{
      type:Boolean,
      default:true
    }
  },
  data() {
    return {
      value:0
    }
  },
  mounted(){
    
  },
  methods:{
    Afun(data){
      console.log("...(3s)");
        if(data >= 30){
          console.log("...(10)");
          let newData = data + 10;
          return Promise.resolve(newData)
        }else{
          return Promise.reject("")
        }
    },
    Bfun(data){
      console.log("B...(10)")
      let newData = data + 10;
      console.log(newData)
    },
    Cfun(data){
      let numberData = Number(data)
      this.Afun(numberData).then(res=>{
        this.Bfun(res)
      }).catch(err=>{
        console.error(err);
      })
    }
  },
}
</script>

<style scoped>
#Promise{
  width: 100%;
}
.inputBox{
  width: 50%;
  display: flex;
}
.input{
  width: calc(100% - 45px);
  height: 30px;
}
.inputBtn{
  width: 45px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.inputBtn button{
  width: 30px;
  height: 30px;
  cursor: pointer;
  border: 2px solid white;
  border-radius: 5px;
  background: none;
  color: white;
}
.input input{
  width: calc(100% - 30px);
  border: none;
  padding: 0 15px;
  height: 100%;
  border-radius: 5px;
  background: rgb(41, 44, 57);
  color: white;
  border: 1px solid rgb(72, 72, 72);
}
</style>