<template>
  <div id="fourPage">
    <div class="leftScrollBox"> 
      <ListStyleOne :tittle="item.tittle" v-for="(item,index) in QAlist" :key="index" :showBefore="item.before">
        <template v-slot:beforeC>
          <component :is="item.component" v-if="!item.before"></component>
        </template>
        <template v-slot:img>
          <img class="img" :src="item.imgURL" :ref="item.refName">
        </template>
        <template v-slot:afterC>
          <component :is="item.component" :before="false"></component>
        </template>
      </ListStyleOne>
    </div>
    <div class="rightScrollBox">
      <div>
        
      </div>
    </div>
