<template>
  <div id="capturing" class="bigBtn" ref="bigBtn" @mousedown="mouseDown(0)" @mouseup="mouseup(0)" v-if="before">
    <div class="bigBtnTextBox">
      <h3>{{bigBtnClick}}</h3>
    </div>
    <div class="smallBtnBox">
      <div class="smallBtn" ref="smallBtn" @mousedown="mouseDown(1)" @mouseup="mouseup(1)">
        <h3>{{smallBtnClick}}</h3>
      </div>
    </div>
  </div>
  <div id="capturing" class="bigBtn" ref="bigBtn" @mousedown.capture="mouseDown(0)" @mouseup.capture="mouseup(0)" v-else>
    <div class="bigBtnTextBox">
      <h3>{{bigBtnClick}}</h3>
    </div>
    <div class="smallBtnBox">
      <div class="smallBtn" ref="smallBtn" @mousedown="mouseDown(1)" @mouseup="mouseup(1)">
        <h3>{{smallBtnClick}}</h3>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name:'Capturing',
  components: {

  },
  props:{
    before:{
      type:Boolean,
      default:true
    }
  },
  data() {
    return {
      bigBtnClick:0,
      smallBtnClick:0,
    }
  },
  methods:{
    mouseDown(whichBtn){ //
      if(whichBtn == 0){
        this.$refs.bigBtn.style.background = "rgb(7, 134, 231)"
      }else{
        this.$refs.smallBtn.style.background = "rgb(255, 0, 0)"
      }
    },
    mouseup(whichBtn){ //
      if(whichBtn == 0){
        this.$refs.bigBtn.style.background = "rgb(6, 105, 181)"
        this.bigBtnClick = this.bigBtnClick + 1
        console.log("")
      }else{
        this.$refs.smallBtn.style.background = "rgb(196, 0, 0)"
        this.smallBtnClick = this.smallBtnClick + 1
        console.log("")
      }  
    }
  },
}
</script>

<style scoped>
@import url(../QAcss.css);
</style>