<template>
  <div id="Debounce">
    <button @click="btnFuntion()"></button>
  </div>
</template>

<script>
import {debounce} from 'lodash'
export default {
  name:'Debounce',
  components: {

  },
  props:{
    before:{
      type:Boolean,
      default:true
    }
  },
  data() {
    return {
      
    }
  },
  methods:{
    btnFuntion(){
      if(this.before){
        console.log("")
      }else{
        debounce(function(){
          console.log("")
        },1000)()
      }
    }
  },
}
</script>

<style scoped>
#Debounce{
  width: 100%;
}

</style>